import React, { useState, useRef, useEffect } from "react";
import "./css/NavChat.css";

export default function NavChat() {
  const [chatOpen, setChatOpen] = useState(false);
  const [username, setUsername] = useState("비회원");
  const [isDragging, setIsDragging] = useState(false);
  const [dragOffset, setDragOffset] = useState({ x: 0, y: 0 });
  const [activeTab, setActiveTab] = useState("home");
  const [messages, setMessages] = useState([]);
  const [isTranslate, setIsTranslate] = useState(false);
  const [isSound, setIsSound] = useState(false);
  const [isSms, setIsSms] = useState(false);
  const [isEmail, setIsEmail] = useState(false);

  const chatBoxRef = useRef(null);
  const chatInputRef = useRef(null);
  const chatContentRef = useRef(null);

  // ✅ username 업데이트: 로그인/로그아웃 즉시 반영
  useEffect(() => {
    const updateUsername = () => {
      const storedUser = localStorage.getItem("loggedInUser");
      setUsername(storedUser ? JSON.parse(storedUser).name : "비회원");
    };

    updateUsername(); // 초기 로드
    window.addEventListener("userChange", updateUsername); // Login/Logout 이벤트
    window.addEventListener("storage", updateUsername); // 다른 탭에서 localStorage 변경 감지

    return () => {
      window.removeEventListener("userChange", updateUsername);
      window.removeEventListener("storage", updateUsername);
    };
  }, []);

  const toggleChat = () => setChatOpen(!chatOpen);

  const scrollToTop = () => window.scrollTo({ top: 0, behavior: "smooth" });
  const scrollToBottom = () =>
    window.scrollTo({ top: document.body.scrollHeight, behavior: "smooth" });

  const predefinedAnswers = [
    {
      keyword: ["배송일정", "배송"],
      answer: `고객님의 주문 상품은\n\n당일 출고 후 당일 배송을 원칙으로 하고 있습니다.\n\n평일 오후 2시 이전 주문 → 당일 발송\n\n평일 오후 2시 이후 주문 → 익일 발송`,
    },
    {
      keyword: ["본사위치", "본사", "위치"],
      answer: `핀아 본사 위치를 안내해드립니다.\n\n핀아 위치\n👉 [지도에서 확인하기]`,
    },
    {
      keyword: ["핀아 소개", "브랜드", "소개", "뜻"],
      answer: `설립연도 : 2025년\n\n핀아의 뜻 : 순 우리말 ‘오랜 정성과 사랑으로 핀 꽃같은 아이’\n\n기획의도 : 삶을 더욱 아름답게 빛내는 시작입니다.\n\n핀아는 이 가치를 담아 고객의 일상에 특별한 순간을 전하고자 합니다.`,
    },
    {
      keyword: ["이벤트", "경품", "뽑기"],
      answer: `현재 진행 중인 이벤트를 안내드립니다.\n\n 현재 진행중인 이벤트는 오늘의 꽃말 뽑기입니다.\n\n👉 [이벤트 바로가기]`,
    },
    {
      keyword: ["상황별 꽃", "추천", "결혼", "우정", "친구", "개업"],
      answer: `상황별 추천 꽃입니다.\n\n결혼 : 백합, 카라, 장미 부케\n\n우정: 프리지아, 해바라기\n\n개업: 난, 호접란, 대형 꽃바구니\n\n👉 [꽃 사러 바로가기]`,
    },
    {
      keyword: ["고객센터", "운영", "문의", "운영시간", "문의사항"],
      answer: `운영시간 : 평일 오전 10시 ~ 오후 6시\n\n토/일/공휴일 휴무\n\n문의사항이 있으시면 아래 버튼을 눌러 글을 남겨주세요.\n\n👉 [문의하기]`,
    },
    {
      keyword: ["인스타그램", "소식"],
      answer: `핀아의 더 많은 소식과 꽃 스타일링은 인스타그램에서 확인하실 수 있습니다.\n\n👉 [인스타그램 바로가기]`,
    },
    {
      keyword: ["이달의 꽃", "이번 달", "다음 달", "9월", "10월"],
      answer: `9월 : 수선화 🌼 — 희망과 새 출발을 상징합니다.\n\n다음 달(10월)의 꽃은 프리지아 🌸 예요.\n\n👉 [2월 꽃 미리 준비하기]`,
    },
    {
      keyword: ["입금계좌", "무통장", "입금", "계좌", "은행"],
      answer: `무통장 입금 시 아래 계좌로 입금 부탁드립니다.\n\n은행명 : 국민은행\n\n계좌번호 : 123-4567-8901-23\n\n예금주 : (주)핀아`,
    },
  ];

  // 사용자 메시지 입력
  const handleKeyPress = (e) => {
    if (!chatInputRef.current) return;
    if (e.key === "Enter" && chatInputRef.current.value.trim() !== "") {
      const userMsg = chatInputRef.current.value.trim();
      let botMsg = "";

      const greetingKeywords = ["안녕", "안녕하세요", "하이"];
      if (greetingKeywords.some((kw) => userMsg.includes(kw))) {
        botMsg = "안녕하세요 ! 핀아입니다 !";
      } else {
        const matched = predefinedAnswers.find((item) =>
          Array.isArray(item.keyword)
            ? item.keyword.some((k) => userMsg.includes(k))
            : userMsg.includes(item.keyword)
        );
        botMsg = matched
          ? matched.answer
          : `${userMsg}에 대해 잘 알지는 못하지만, 열심히 공부중이에요!`;
      }

      setMessages((prev) => [
        ...prev,
        { sender: "user", text: userMsg },
        { sender: "bot", text: botMsg },
      ]);
      setActiveTab("chat");
      chatInputRef.current.value = "";
    }
  };

  // 드래그 이벤트
  const handleMouseDown = (e) => {
    setIsDragging(true);
    const rect = chatBoxRef.current.getBoundingClientRect();
    setDragOffset({ x: e.clientX - rect.left, y: e.clientY - rect.top });
  };

  const handleMouseMove = (e) => {
    if (!isDragging) return;
    let left = e.clientX - dragOffset.x;
    let top = e.clientY - dragOffset.y;

    const maxLeft = window.innerWidth - chatBoxRef.current.offsetWidth;
    const maxTop = window.innerHeight - chatBoxRef.current.offsetHeight;
    left = Math.max(0, Math.min(left, maxLeft));
    top = Math.max(0, Math.min(top, maxTop));

    chatBoxRef.current.style.left = left + "px";
    chatBoxRef.current.style.top = top + "px";
    chatBoxRef.current.style.bottom = "auto";
    chatBoxRef.current.style.right = "auto";
    chatBoxRef.current.style.position = "fixed";
  };

  const handleMouseUp = () => setIsDragging(false);

  useEffect(() => {
    document.addEventListener("mousemove", handleMouseMove);
    document.addEventListener("mouseup", handleMouseUp);
    return () => {
      document.removeEventListener("mousemove", handleMouseMove);
      document.removeEventListener("mouseup", handleMouseUp);
    };
  });

  // 채팅 자동 스크롤
  useEffect(() => {
    if (activeTab === "chat" && chatContentRef.current) {
      chatContentRef.current.scrollTop = chatContentRef.current.scrollHeight;
    }
  }, [messages, activeTab]);

  return (
    <>
      {/* 버튼 영역 */}
      <div id="navIcon">
        <div id="chatBot" onClick={toggleChat}>
          <svg
            className="cbImg"
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 24 24"
          >
            <g
              fill="none"
              stroke="currentColor"
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth="2"
            >
              <path d="M18 4a3 3 0 0 1 3 3v8a3 3 0 0 1-3 3h-5l-5 3v-3H6a3 3 0 0 1-3-3V7a3 3 0 0 1 3-3zM9.5 9h.01m4.99 0h.01" />
              <path d="M9.5 13a3.5 3.5 0 0 0 5 0" />
            </g>
          </svg>
        </div>

        <div id="updownBtn">
          <svg className="upBtn" onClick={scrollToTop} viewBox="0 0 16 7">
            <path
              fill="currentColor"
              d="M12.5 6a.47.47 0 0 1-.35-.15L8 1.71L3.85 5.85c-.2.2-.51.2-.71 0s-.2-.51 0-.71L7.65.65c.2-.2.51-.2.71 0l4.5 4.5c.2.2.2.51 0 .71c-.1.1-.23.15-.35.15Z"
            />
          </svg>
          <svg className="downBtn" onClick={scrollToBottom} viewBox="0 0 16 7">
            <path
              fill="currentColor"
              d="M8 6.5a.47.47 0 0 1-.35-.15l-4.5-4.5c-.2-.2-.2-.51 0-.71s.51-.2.71 0l4.15 4.15l4.14-4.14c.2-.2.51-.2.71 0s.2.51 0 .71l-4.5 4.5c-.1.1-.23.15-.35.15Z"
            />
          </svg>
        </div>
      </div>

      {/* 챗봇 박스 */}
      {chatOpen && (
        <div id="chatBox" ref={chatBoxRef} onMouseDown={handleMouseDown}>
          <div id="chatHeader">
            <button
              type="button"
              className="cbChat"
              onClick={() => setActiveTab("chat")}
              style={{ color: activeTab === "chat" ? "#6C5242" : "#FFFEF8" }}
            >
              대화
            </button>
            <button
              type="button"
              className="cbHome"
              onClick={() => setActiveTab("home")}
              style={{ color: activeTab === "home" ? "#6C5242" : "#FFFEF8" }}
            >
              홈
            </button>
            <button
              type="button"
              className="cbSet"
              onClick={() => setActiveTab("set")}
              style={{ color: activeTab === "set" ? "#6C5242" : "#FFFEF8" }}
            >
              설정
            </button>
          </div>

          <div id="chatContent" ref={chatContentRef}>
            {activeTab === "home" && (
              <>
                <div className="cbHi">
                  안녕하세요, 핀아 AI 상담사입니다.
                  <br />
                  궁금한 점을 질문해주시면
                  <br />
                  안내해드릴게요!
                </div>

                <div className="cbBtns">
                  {predefinedAnswers.map((item, index) => (
                    <div
                      key={index}
                      className={`cbnineBtn${index + 1}`}
                      onClick={() => {
                        setMessages((prev) => [
                          ...prev,
                          { sender: "user", text: item.keyword[0] },
                          { sender: "bot", text: item.answer },
                        ]);
                        setActiveTab("chat"); // 자동 채팅 탭 전환
                      }}
                    >
                      {item.keyword[0]}
                    </div>
                  ))}
                </div>
              </>
            )}

            {activeTab === "chat" && (
              <div className="chatMessages">
                {messages.map((msg, idx) => (
                  <div
                    key={idx}
                    className={
                      msg.sender === "user" ? "userMessage" : "botMessage"
                    }
                  >
                    {msg.text}
                  </div>
                ))}
              </div>
            )}

            {activeTab === "set" && (
              <div>
                <div className="cbsetcon1">
                  <div className="setimgbox">
                    <img
                      src="https://kjwon2025.github.io/pinaimg/chatbotprofile.jpg"
                      alt="chatbotprofile"
                    />
                  </div>
                  <span className="setusername">{username}</span>
                </div>
                <div className="cbsetcon2">
                  <div className="setspanbox1">
                    <span>언어</span>
                    <span>한국어</span>
                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48">
                      <path
                        fill="none"
                        stroke="currentColor"
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        strokeWidth="4"
                        d="m19 12l12 12l-12 12"
                      />
                    </svg>
                  </div>
                  <div className="setspanbox2">
                    <span>메세지 번역 표시</span>
                    <div
                      className="setbtnon"
                      onClick={() => setIsTranslate(!isTranslate)}
                      style={{
                        backgroundColor: isTranslate ? "#6C5242" : "#bbb",
                      }}
                    >
                      <div
                        className="btncircle"
                        style={{ left: isTranslate ? "22px" : "6px" }}
                      ></div>
                    </div>
                  </div>
                  <div className="setspanbox3">
                    <span>알림음</span>
                    <div
                      className="setbtnon"
                      onClick={() => setIsSound(!isSound)}
                      style={{ backgroundColor: isSound ? "#6C5242" : "#bbb" }}
                    >
                      <div
                        className="btncircle"
                        style={{ left: isSound ? "22px" : "6px" }}
                      ></div>
                    </div>
                  </div>
                </div>
                <div className="cbsetcon3">
                  <div className="setspanbox4">
                    <span>문자 수신 거부</span>
                    <div
                      className="setbtnon"
                      onClick={() => setIsSms(!isSms)}
                      style={{ backgroundColor: isSms ? "#6C5242" : "#bbb" }}
                    >
                      <div
                        className="btncircle"
                        style={{ left: isSms ? "22px" : "6px" }}
                      ></div>
                    </div>
                  </div>
                  <div className="setspanbox5">
                    <span>이메일 수신 거부</span>
                    <div
                      className="setbtnon"
                      onClick={() => setIsEmail(!isEmail)}
                      style={{ backgroundColor: isEmail ? "#6C5242" : "#bbb" }}
                    >
                      <div
                        className="btncircle"
                        style={{ left: isEmail ? "22px" : "6px" }}
                      ></div>
                    </div>
                  </div>
                </div>
              </div>
            )}
          </div>

          <input
            ref={chatInputRef}
            type="text"
            placeholder="질문을 입력하세요"
            onKeyPress={handleKeyPress}
            id="chatInput"
          />
        </div>
      )}
    </>
  );
}
