import { useState, useEffect } from "react";
import axios from "axios";

import NewsItem from "./NewsItem";
import "./css/NewsList.css";

const NewsList = () => {
  const [articles, setArticles] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [selectedCategory, setSelectedCategory] = useState("general");

  const categories = [
    { key: "general", label: "전체" },
    { key: "business", label: "비즈니스" },
    { key: "entertainment", label: "연예" },
    { key: "health", label: "건강" },
    { key: "science", label: "과학" },
    { key: "sports", label: "스포츠" },
    { key: "technology", label: "기술" },
  ];

  const fetchData = async (category) => {
    setLoading(true);
    setError(null);

    try {
      const fromDate = new Date();
      fromDate.setDate(fromDate.getDate() - 7);
      const fromDateStr = fromDate.toISOString().split("T")[0];

      const response = await axios.get(
        `https://newsapi-68cj.onrender.com/news?category=${category}&fromDate=${fromDateStr}`
      );

      // ✅ 유효한 이미지 URL만 필터링
      const filteredArticles = (response.data.articles || []).filter(
        (article) =>
          article.urlToImage &&
          /^https?:\/\/.+\.(jpg|jpeg|png|webp|gif)$/i.test(article.urlToImage)
      );

      setArticles(filteredArticles);
    } catch (e) {
      console.error(
        "뉴스 API 호출 실패:",
        e.response ? e.response.data : e.message
      );
      setError("뉴스 데이터를 불러오는 중 오류가 발생했습니다.");
      setArticles([]); // 에러 시 빈 배열로 초기화
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchData(selectedCategory);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [selectedCategory]);

  return (
    <div className="news-list-wrapper">
      <div className="category-buttons">
        {categories.map((cat) => (
          <button
            key={cat.key}
            className={`category-btn ${
              selectedCategory === cat.key ? "active" : ""
            }`}
            onClick={() => setSelectedCategory(cat.key)}
          >
            {cat.label}
          </button>
        ))}
      </div>

      <div className="news-list-block">
        {loading && <div>대기중...</div>}
        {error && <div>{error}</div>}
        {!loading && !error && !articles.length && <div>뉴스가 없습니다.</div>}

        {!loading && !error && (
          <div className="news-list-grid">
            {articles.slice(0, 4).map((article) => (
              <NewsItem key={article.url} article={article} />
            ))}
          </div>
        )}
      </div>
    </div>
  );
};

export default NewsList;
