import { useState } from "react";
import "./css/NewsItem.css";

const NewsItem = ({ article }) => {
  const [imgError, setImgError] = useState(false); // 이미지 에러 상태 관리
  const { title, description, url, urlToImage } = article;

  const fallbackImage = "/img/logo.png";

  // HTTP → HTTPS 강제 변환
  const secureImageUrl = urlToImage
    ? urlToImage.replace(/^http:\/\//i, "https://")
    : fallbackImage;

  // weserv.nl 프록시 적용 (https:// 제거 후 proxy 서버 통해 로드)
  const proxiedImageUrl = secureImageUrl.startsWith("http")
    ? `https://images.weserv.nl/?url=${encodeURIComponent(
        secureImageUrl.replace(/^https?:\/\//, "")
      )}`
    : secureImageUrl;

  // 이미지 로드 실패 시 해당 뉴스 아이템 아예 렌더링하지 않음
  if (imgError) return null;

  return (
    <div className="news-item">
      <div className="thumbnail">
        <a href={url} target="_blank" rel="noopener noreferrer">
          <img
            src={proxiedImageUrl}
            alt="thumbnail"
            onError={() => setImgError(true)} // ❗ 로드 실패 → 아이템 제거
          />
        </a>
      </div>

      <div className="contents">
        <h2>
          <a href={url} target="_blank" rel="noopener noreferrer">
            {title}
          </a>
        </h2>
        <p>{description}</p>
      </div>
    </div>
  );
};

export default NewsItem;
